import { Outlet, Link } from "react-router-dom";

const Layout = () => {
  return (
    <>
      <nav
      className="ugglyindex"
      >
        <ul>
          <li>
            <Link to="/">Home</Link>
          </li>
          <li>
            <Link to="/drinks">Drinks</Link>
          </li>
          <li>
            <Link to="/queue">Queue</Link>
          </li>
          <li>
            <Link to="/switch">Switch</Link>
          </li>
        </ul>
      </nav>

      <Outlet />
    </>
  )
};

export default Layout;